<template>
        <Transition name="modal">
              <div class="modal" @click="closModal">
    <div class="modal__block" @click.stop="">
      <h2 class="modal__block-title">{{edit ? 'Изменть заметку' : 'Добавить заметку'}}</h2>
      <div class="modal__block-inputs">
        <label>
          <span>Title</span>
          <input v-model.trim="title" type="text" />
        </label>
        <label>
          <span>Content</span>
          <textarea v-model.trim="text"></textarea>
        </label>
      </div>
      <div class="modal__block-btns">
        <button class="modal__block-btn del" @click="closModal">Отмена</button>
        <button v-if="!edit" class="modal__block-btn edit"  @click="addNote">Добавить</button>
        <button v-else class="modal__block-btn edit"  @click="edditeNote">Изменить</button>
      </div>
    </div>
  </div>
        </Transition>
</template>

<script>
export default {
    props:{
        edit:Boolean,
        editeNote: Object
    },
  data() {
    return {
      title: "",
      text: "",
    };
  },
  methods:{
    closModal(){
        this.$emit('closeModal')
         this.title = this.text =''
    },
    addNote(){
        if(this.title != '' && this.text != ''){
                 const note = {
            title: this.title,
            text: this.text,
            date: new Date().toLocaleDateString()
        }
        this.$emit('addNote', note)   
         this.title = this.text =''
        }
    },
    edditeNote(){
      if(this.title != '' && this.text != ''){
          const editNote = {
            id: this.editeNote.id,
            title: this.title,
            text: this.text,
            date: new Date().toLocaleDateString()
          } 
          this.$emit('editeNewNote', editNote)
          this.closModal()
      } 
    }
  }
};
</script>

