<template>
    <div class="notes">
        <div class="container">
            <div class="notes__top">
                <h2 class="notes__top-title">{{ notes.lenght > 0 ? 'Все заметки' : 'Нет заметок'}}</h2>
                <button class="notes__top-btn" @click="view = !view">
                    <img src="@/assets/img/list.svg" alt="" v-show="view">
                    <img src="@/assets/img/grid.svg" alt="" v-show="!view">
                    <span>
                        {{view ? 'Список' : 'сетка'}}
                    </span>
                </button>
            </div>
            <div class="notes__list" :class="{ active: !view }">
                <Notesitem 
                v-for="note in notes" 
                :key="note.id"
                :note="note"
                :view="view"
                @delNote="$emit('delNote', note.id)"
                @changeNote="$emit('changeNote', note.id)" 
                />
            </div>
        </div>
    </div>
</template>

<script>

import Notesitem from '@/components/Notesitem.vue'

export default {
    components:{
        Notesitem
    },
    data(){
        return{
            view: true
        }
    },
    props:{
        notes: Array
    }
}
</script>

